<template>
    <div class="tel container">
        <div class="tel__item" v-for="tel in tels" :key="tel.id">
            <RouterLink :to="/about/ + tel.id">
                <img :src="tel.img" alt="">
            </RouterLink>
            
            <div class="tel__item_desc">
                <h2 class="tel__title">{{ tel.title }}</h2>
                <p class="tel__text">{{ tel.text }}</p>
            </div>
        </div>

    </div>
</template>

<script>

import { items } from "@/items";

export default {
    data() {
        return {
            tels: items,
        }
    },
}
</script>
